* {
    margin: 0;
    padding: 0;
}

.main {
    background: rgb(0, 0, 0);
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#name {
    color: white;
    z-index: 2;
}

nav {
    /* background: rgb(30, 30, 30); */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 70px;
    z-index: 10;
    position: fixed;
    width: 100%;
}

nav ul {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    gap: 30px;
    margin-right: 30px;
}

.nav-links {
    list-style: none;
}

.nav-links a {
    text-decoration: none;
    font-size: 18px;
    color: white;
    margin: 8px;
}




.introduction {
    height: 100vh;
    background: rgb(30, 30, 30);
    display: grid;
    grid-template-columns: 1fr 5fr;
    color: white;
    padding: 5%;
    align-items: center;
}

#story {
    color: rgb(222, 200, 0);
    writing-mode: vertical-lr;
    font-size: 64px;
    width: fit-content;
}

#content {
    font-size: 18px;
}

#vishnu {
    height: 100%;
    position: absolute;
    opacity: 0.5;
    /* filter: drop-shadow(4px 4px   rgb(195, 195, 195)); */
}

#overlay {
    height: 100vh;
    width: 100%;
    position: absolute;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
    z-index: 1;
}

/* video{
    position: absolute;
    height: 100vh;
    width: 100%;
    z-index: -1;
} */
.hamburger {
    display: none;
}
.hamburger-menu{
    display: none;
}
@media only screen and (max-width: 800px) {
    nav {
        display: none;
    }

    .hamburger {
        position: fixed;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 10px;
        align-items: center;
        right: 0;
        margin: 1em 1em;
        padding: 0.8em;
        z-index: 999;
        border-radius: 20%;
        background: rgba(0, 0, 0, 0.7);
        transition: transform 0.3s ease;
        
    }

    .bar {
        height: 4px;
        background: white;
        width: 50px;
        border-radius: 2px;
        transition: transform 0.3s ease;
    }

    #name {
        font-size: 300%;
    }

    #vishnu {
        height: 100vh;
        max-width: 100vw;
        object-fit: cover;
    }

    .open #one {
        transform: rotate(45deg) translateX(8px);
        -webkit-transform: rotate(45deg) translateX(8px);
        transform-origin: 30%;
        -webkit-transform-origin: 30%;
    }

    .open #two {
        transform: rotate(-45deg) translateY(10px);
        -webkit-transform: rotate(-45deg) translateY(10px);
        transform-origin: 22%;
        -webkit-transform-origin: 22%;
    }

    .open #three {
        display: none;
    } 
    
    .hamburger-menu {
        display: none;
    }
    
    .open-menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        height: 100vh;
        background: rgba(0,0,0);
        position: fixed;
        width: 100%;
        z-index: 98;
    }
    #menu-list{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        height: 90vh;
    }
    .menu-item {
        list-style: none;
        font-size: 3em;
        color: white;
        font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        font-weight: 800;
        text-decoration: none;
        text-transform: uppercase;
    }
    
}









