* {
    margin: 0;
    padding: 0;
}

.main-div {
    min-height: 100vh;
    background: rgb(30, 30, 30);
    display: grid;
    grid-template-columns: 5fr 1fr;
    color: white;
    padding: 2%;
    align-items: center;
}

#project {
    color: rgb(222, 200, 0);
    writing-mode: vertical-lr;
    font-size: 64px;
    width: fit-content;
}

.project-grid {
    display: flex;
    flex-direction: column;
    gap: 3rem;
}
.proj-demo{
    display: flex;
    flex-direction: column;
    align-items: center;
}
#proj-decs{
    text-align: justify;
    margin-top: 10px;
}
.project-card {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
}

#top-project-img{
    width: 80%;
    padding: 5%;
}
.project-card:nth-child(odd) {
    order: 1;
}


.projects-main-div {
    background-color: black;
    min-height: 100vh;
}

.projects-main-div h1 {
    padding-top: 7%;
    color: white;
}

.project_url {
    color: white;
}

.icon {
    padding: 10px 20px;
}

.link-icons {
    /* margin: 10px; */
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.skill-set{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 3%;
}
.vertical-timeline-element-subtitles {
    /* From https://css.glass */
    background: rgba(255, 255, 255, 0.2);
    border-radius: 4px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    text-align: center;
    padding: 10px;
    margin-bottom: 10px;
    transition: 1s;
}
.vertical-timeline-element-subtitles:hover{
    transform: scale(1.2);
}
.service-main{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: black;
    gap: 10%;
}
.services{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 30px;
    margin: 20px;
}
.service_card{
    border: 5px solid white;
    cursor: pointer;
    border-radius: 2%;
    transition: 1s ease;
}
.services img{
    width: 100%;
}
#service-title{
    color: white;
}
.service_card:hover{
    transition: 1s;
    transform: scale(1.1);
}

@media only screen and (max-width: 800px) {
    .main-div{
        display: flex;
        flex-direction: column-reverse;
    }
    #project{
        writing-mode: horizontal-tb;
        font-size: 3.5em;
    }
    .project-card{
        margin-top: 30px;
        display: flex;
        flex-direction: column;
        border: 1px solid white;
        padding: 20px;
        border-radius: 20px;
        background: linear-gradient(45deg, rgb(65, 65, 65), rgb(0, 0, 0));
        box-shadow: 4px 4px rgba(255,255,255,0.4);
    }
    #project-head{
        font-size: 3.5em;
    }
    .skill-set{
        grid-template-columns: repeat(2,1fr);
    }
    #service-title{
        font-size: 4em;
        margin-top: 15%;
    }
    .services{
        display: flex;
        flex-direction: column;
    }
    .service_card:hover{
        transform: scale(1);
    }
}